import { useState, useCallback } from "react";
import Header from "./Header";
import SearchBar from "./SearchBar";
import ResultMovies from "./ResultMovies";
import "./index.scss";

interface IInputField {
  title: string;
  type: string;
  year: string;
}

function App() {
  const [movies, setMovies] = useState<any>({});
  const [isLoading, setIsLoading] = useState<boolean>(false);
  const [inputField, setInputField] = useState<IInputField>({
    title: "",
    type: "",
    year: "",
  });
  const [pageNum, setPageNum] = useState<number>(1);

  function callAPI(title: string, type: string, year: string, pageNum: number) {
    setIsLoading(true);
    setPageNum(pageNum);
    // console.log(title);
    fetch(
      "https://www.omdbapi.com/?apikey=29b4cbb&s=" +
        title +
        "&type=" +
        type +
        "&y=" +
        year +
        "&page=" +
        pageNum
    )
      .then((res) => res.json())
      .then(
        (result) => {
          setMovies(result);
          // console.log(result);
          setIsLoading(false);
        },
        (error) => {
          // console.log(error);
          setIsLoading(false);
        }
      );
  }

  function pageNumChange(num: number) {
    callAPI(inputField.title, inputField.type, inputField.year, num);
  }

  function getInputFieldData(title: string, type: string, year: string) {
    setInputField({ title: title, type: type, year: year });
  }

  return (
    <div className="app-body">
      <div className="app-content">
        <Header />
        <div className="app-body">
          <SearchBar callAPI={callAPI} getInputFieldData={getInputFieldData} />
          <ResultMovies
            movies={movies}
            isLoading={isLoading}
            pageNumChange={pageNumChange}
            pageNum={pageNum}
          />
        </div>
      </div>
    </div>
  );
}

export default App;
